<template>
    <div id="app" :class="{'hidden-slides-viewer': !slidesViewer}">
        <div id="mainContent" :class="{'half-mode': enableHalfMode}">
            <transition name="fade">
                <div id="shareMessage" v-if="shareMessage">Copied to the clipboard.</div>
            </transition>
            <slides-viewer v-show="slidesViewer" id="slidesViewer" ref="slidesViewer"
                           :source="getSource()"></slides-viewer>
            <div id="rightSideBar">
                <brightcove-video ref="brightcoveVideo"
                                  :accountCode="currentVideo.accountCode"
                                  :playerCode="currentVideo.playerCode"
                                  :videoID="currentSlide.videoID"
                                  :time="currentVideoTime"
                                  :currentSlideIndex="currentSlideIndex"
                                  @timeupdate="onTimeUpdate"
                                  @ended="onEnded">
                </brightcove-video>
                <slides-list ref="slidesList"
                             :slides="slides"
                             :startSlideIndex="currentSlideIndex"
                             :subSlideIndex="currentSubSlideIndex"
                             :showSubSlides="showSubSlides"
                             :subSlidesActive="subSlidesActive"
                             @changeActiveItem="goToSlide">
                </slides-list>
            </div>
        </div>
        <div class="footer">
            <div id="controlBtnWrapper1">
                <button class="control-btn" id="goToPrev" title="Previous slide"
                        :class="{'disabled':isGoToPrevDisabled}"
                        @click="goToSlide({currentSlideIndex: --currentSlideIndex})"></button>
                <button class="control-btn" id="goToNext" title="Next slide"
                        :class="{'disabled':isGoToNextDisabled}"
                        @click="goToSlide({currentSlideIndex: ++currentSlideIndex})"></button>
            </div>
            <div id="controlBtnWrapper2">
                <button class="control-btn" id="shareBtn" title="Share slide" @click="share"></button>
                <button class="control-btn" id="toggleModeBtn" title="Change slide layout"
                        @click="toggleHalfMode" :class="{'active':enableHalfMode}"></button>
                <button class="control-btn" id="fullScreenVideoBtn" title="Video Full Screen"
                        @click="enterFullScreenVideo"></button>
                <button class="control-btn" id="fullScreenSlideBtn" title="Slide Full Screen"
                        @click="enterFullScreenSlide"></button>
            </div>
        </div>
    </div>
</template>

<script>
    import slidesList from "./components/slides-list.vue";
    import slidesViewer from "./components/slides-viewer";
    import brightcoveVideo from "./components/brightcove-video";
    import vueConfig from "../vue.config";
    import {copyStringToBuffer, getUrlParameter} from "./utils";

    const configJsonPath = `${vueConfig.publicPath}/config.json`;

    export default {
        name: "aem-video-slides",
        components: {
            slidesList,
            slidesViewer,
            brightcoveVideo
        },
        data: function () {
            return {
                enableHalfMode: false,
                shareMessage: false,
                currentSlideIndex: undefined,
                currentSubSlideIndex: 0,
                configuration: {},
                slides: [],
                slidesViewer: false,
                showSubSlides: false,
                subSlidesActive: false,
                currentVideoTime: 0,
                currentSlide: {
                    title: "",
                    videoID: "",
                    imageUrl: "",
                    time: 0,
                    subSlides: []
                },
                currentVideo: {
                    accountCode: "",
                    playerCode: ""
                }
            }
        },
        computed: {
            isGoToPrevDisabled() {
                return (this.slides.length === 0) || (this.currentSlideIndex === 0);
            },
            isGoToNextDisabled() {
                return (this.slides.length === 0) || (this.currentSlideIndex === this.slides.length - 1);
            }
        },
        watch: {
            currentSlideIndex() {
                this.currentSlide = this.slides[this.currentSlideIndex];
            }
        },
        methods: {
            getSource() {
                let imageUrl = this.currentSlide.imageUrl;
                if (this.currentSlide.subSlides) {
                    this.currentSlide.subSlides.forEach((subSlide) => {
                        if (this.currentVideoTime >= subSlide.time) {
                            imageUrl = subSlide.imageUrl;
                        }
                    });
                }
                return imageUrl;
            },
            onTimeUpdate(currentTime) {
                console.log(Math.floor(currentTime / 60) + ': ' + currentTime % 60);
                let slideIndex = 0;
                let subSlideIndex;

                this.slides.forEach((slide, index) => {
                    if (currentTime >= slide.time) slideIndex = index;
                });
                this.slides[slideIndex].subSlides && this.slides[slideIndex].subSlides.forEach((subSlide, index) => {
                    if (currentTime >= subSlide.time) subSlideIndex = index;
                });

                this.currentSlideIndex = slideIndex;
                this.currentSubSlideIndex = subSlideIndex;
                this.currentVideoTime = currentTime;
            },
            onEnded() {
                (this.currentSlideIndex !== this.slides.length - 1) && this.goToSlide(++this.currentSlideIndex);
            },
            goToSlide(slide) {
                if (slide.currentSubSlideIndex !== undefined) {
                    this.currentVideoTime = this.slides[slide.currentSlideIndex].subSlides[slide.currentSubSlideIndex].time;
                } else {
                    this.currentVideoTime = this.slides[slide.currentSlideIndex].time;
                }
                this.currentSlideIndex = slide.currentSlideIndex;
                this.currentSubSlideIndex = undefined;
                this.$refs.brightcoveVideo.play();
            },
            enterFullScreenVideo() {
                this.$refs.brightcoveVideo.enterFullScreen();
            },
            toggleHalfMode() {
                this.enableHalfMode = !this.enableHalfMode;
            },
            enterFullScreenSlide() {
                this.$refs.slidesViewer.enterFullScreen();
            },
            share() {
                let url;
                const param = `currentSlideIndex=${this.currentSlideIndex + 1}`;
                if (location.href.includes("currentSlideIndex")) {
                    const regex = new RegExp("currentSlideIndex" + '=([^&#]*)');
                    url = location.href.replace(regex, param);
                } else {
                    url = `${location.href}?${param}`;
                }

                copyStringToBuffer(url, this.$el);

                this.shareMessage = true;
                setTimeout(() => this.shareMessage = false, 2000);
            }
        },
        created() {
            const xhr = new XMLHttpRequest();
            xhr.open("GET", configJsonPath, false);
            xhr.send();
            if (xhr.readyState === 4 && xhr.status === 200) {
                this.configuration = JSON.parse(xhr.response);
                this.slidesViewer = this.configuration.slidesViewer;
                this.showSubSlides = this.configuration.showSubSlides;
                this.subSlidesActive = this.configuration.subSlidesActive;

                this.slides = this.configuration.chapters.reduce((slides, chapter) => {
                    chapter.slides.forEach(slide => {
                        slide.active = false;
                        slide.videoID = chapter.videoID
                    });
                    return slides.concat(chapter.slides)
                }, []);

                const sharedSlideIndex = getUrlParameter("currentSlideIndex");

                if (sharedSlideIndex > 0 && sharedSlideIndex < this.slides.length) this.currentSlideIndex = sharedSlideIndex;
                else this.currentSlideIndex = 0;
                this.currentSlide = this.slides[this.currentSlideIndex];
                this.currentVideoTime = this.slides[this.currentSlideIndex].time;
                this.currentSlide.active = true;
                this.currentVideo.accountCode = this.configuration.accountCode;
                this.currentVideo.playerCode = this.configuration.playerCode;
            }
        }
    }
</script>

<style>
    @import './assets/style/mobile.css';

    #app {
        width: 100%;
        height: 567px;
        max-width: 1024px;
    }

    #mainContent {
        position: relative;
        display: flex;
        width: 100%;
        height: 507px;
    }

    #slidesViewer {
        display: flex;
        align-items: center;
        width: 66%;
        height: 100%;
    }

    #rightSideBar {
        width: 34%;
        height: 100%;
    }

    #videoWrapper {
        width: 100%;
        height: 196px;
    }

    #slidesList {
        width: 100%;
        height: 311px;
        overflow: auto;
    }

    .half-mode #slidesViewer,
    .half-mode #rightSideBar {
        width: 50%;
    }

    .half-mode #videoWrapper {
        height: 288px;
    }

    .half-mode #slidesList {
        height: 219px;
    }

    .footer {
        position: relative;
        height: 60px;
        box-sizing: border-box;
        background: #ececec;
        border-bottom: 20px solid #74a187;
        display: flex;
        justify-content: space-between;
        padding: 10px;
    }

    #controlBtnWrapper1 {
        display: flex;
        justify-content: space-between;
        width: 85px;
    }

    #controlBtnWrapper2 {
        display: flex;
        justify-content: space-between;
        width: 186px;
    }

    .control-btn {
        width: 40px;
        height: 40px;
        border: none;
        outline: none;
        cursor: pointer;
    }

    .control-btn.disabled {
        opacity: 0.6;
        pointer-events: none;
    }

    #goToPrev {
        background: transparent url("assets/arrowLeft.png") no-repeat center/contain;
    }

    #goToNext {
        background: transparent url("assets/arrowRight.png") no-repeat center/contain;
    }

    #shareBtn {
        background: transparent url("assets/share-btn.png") no-repeat center/contain;
    }

    #toggleModeBtn {
        background: transparent url("assets/toggle-mode-btn.png") no-repeat center/contain;
    }

    #toggleModeBtn.active {
        background: transparent url("assets/toggle-mode-btn-active.png") no-repeat center/contain;
    }

    #fullScreenVideoBtn {
        background: transparent url("assets/full-screen-video-btn.png") no-repeat center/contain;
    }

    #fullScreenSlideBtn {
        background: transparent url("assets/full-screen-slide-btn.png") no-repeat center/contain;
    }

    #shareMessage {
        position: absolute;
        top: 20px;
        left: 20px;
        background-color: #323232;
        color: #f1f1f1;
        text-align: center;
        height: 48px;
        width: 250px;
        padding: 16px 24px;
        box-sizing: border-box;
        box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
        border-radius: 2px;
        font-size: 14px;
        cursor: default;
        z-index: 10;
    }

    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }

    .fade-enter {
        opacity: 0;
    }

    .hidden-slides-viewer #rightSideBar {
        display: flex;
        width: 100%;
    }

    .hidden-slides-viewer #videoWrapper {
        width: 67%;
        height: 100%;
    }

    .hidden-slides-viewer #slidesList {
        width: 33%;
        height: 100%;
    }

    .hidden-slides-viewer #controlBtnWrapper1,
    .hidden-slides-viewer #fullScreenSlideBtn,
    .hidden-slides-viewer #toggleModeBtn {
        display: none;
    }

    .hidden-slides-viewer .footer {
        justify-content: flex-end;
    }

    .hidden-slides-viewer #controlBtnWrapper2 {
        width: 90px;
    }
</style>
